import React, {useRef} from "react";
import Logo from "../Logo";
import {useRouteMatch, useHistory, useLocation, Switch, Route} from 'react-router-dom';
import Container from "react-bootstrap/Container";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import styles from './styles.module.scss';
import cn from 'classnames';
import useDropDown from "../../../common/hooks/useDropDown";
import Row from "react-bootstrap/Row";

function Menu() {
  const { path, url } = useRouteMatch();

  const pageTabs = [{
    title: 'ГЛАВНАЯ',
    path: '',
  },{
    title: 'О НАС',
    // className: 'd-none d-sm-flex',
    path: 'about',
  },{
    title: 'ФОНД',
    // className: 'd-none d-sm-flex',
    dropDown: [{
      name: 'Награды и достижения',
      path: 'awards',
    },{
      name: 'Почему "НАШ ФОНД"?',
      path: 'whyUs',
    },{
      name: 'Преимущества',
      path: 'advantages',
    },{
      name: 'Клиентское соглашение',
      href: require('../../../docs/Client_Agreement.docx')
    },{
      name: 'Борьба с отмыванием денег',
      href: require('../../../docs/Борьба_с_отмыванием_денег.docx'),
    },{
      name: 'Политика конфиденциальности',
      href: require('../../../docs/ПОЛИТИКА_КОНФИДЕНЦИАЛЬНОСТИ.docx'),
    },{
      // todo
      name: 'Экономический календарь',
    }],
  },{
    title: 'УСЛУГИ',
    // className: 'd-none d-sm-flex',
    dropDown: [{
      name: 'Персональный консультант',
      path: 'assistant',
    },{
      name: 'Инвестиционный портфель',
      path: 'portfel',
    },{
      name: 'Фонд "НАШ ФОНД"',
      path: 'fund',
    },],
  },{
    title: 'КАРЬЕРА',
    // className: 'd-none d-sm-flex',
    path: 'career',
  },{
    title: 'КОНТАКТЫ',
    // className: 'd-none d-sm-flex',
    className: 'mr-lg-2',
    path: 'contacts',
  }];

  const estTabs = [{
    title: 'ОТКРЫТЬ СЧЁТ',
    path: 'register',
  },{
    title: 'ВОЙТИ',
    path: 'login',
  }];

  const containerRef = useRef(null);

  return (
    <div className={styles.holder}>
      <Container className={cn(
        styles.container,
        'justify-content-center',
        'd-block d-lg-flex'
      )}>
        <Logo className={cn(styles.logo, 'd-none d-lg-block mr-lg-4')}/>
        <Row
          className={cn(
            styles.tabs,
            'justify-content-center'
          )}
          ref={containerRef}
        >
          {pageTabs.map((item) => Tab({...item, containerRef}))}
          {estTabs.map(Tab)}
        </Row>
        {/*<Row className={styles.tabs}>*/}
        {/*  {estTabs.map(Tab)}*/}
        {/*</Row>*/}
      </Container>
    </div>
  );
}

function Tab({title, className, path:pathEnd, dropDown, containerRef, href}) {
  const history = useHistory();
  const {pathname} = useLocation();

  const isActive = pathname.split('/').pop() === pathEnd;
  // const isActive = Boolean(~pathname.indexOf(pathEnd));

  const ref = useRef(null);
  const dropDownElement = useDropDown({dropDown, ref, containerRef});


  return (
    <>
      {dropDownElement}
      <TouchableOpacity
        ref={ref}
        className={cn(
          styles.tab,
          isActive && styles.active,
          className
        )}
        // todo
        onClick={() => {
          pathEnd !== undefined && history.push(`/${pathEnd}`);
        }}
      >
        {title}
      </TouchableOpacity>
    </>
  );
}

export default Menu;
