import React from 'react';
import Header from '../../Header';
import styles from './styles.module.scss';
import Awards from "../Main/components/Awards";
import About from "../Main/components/About";
import Container from "react-bootstrap/Container";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

// todo
function AboutGlobal() {
  return (
    <>
      <Header
        title="О НАС"
        className={styles.header}
      />
      <Awards/>
      <div className={styles.aboutWithBg}>
        <Container>
          <Row>
            <Col className="text-justify p-3" xs={12} lg={6}>
              «НАШ ФОНД» - это команда трейдеров, аналитиков и юристов, с богатым опытом работы в коммерческих и некоммерческих организациях, а также государственных финансовых институтах. Наши эксперты, обладают нужными качествами, опытом и знаниями в своей области, для решения любых задач. Наша команда более 5-ти лет успешно развивает инвестиционные портфели и создает многоуровневые системы торговли на рынках ценных бумаг.
            </Col>
            <Col className="text-justify p-3" xs={12} lg={6}>
              Более 1300 инвесторов работают с нами на протяжении многих лет, что делает нашу команду нацеленной на долгосрочное сотрудничество с каждым инвестором «НАШЕГО ФОНДА». Прозрачность торгов и максимальная доступность к средствам, даст вам уверенность в завтрашнем дне! Мы покажем Вам как в команде из 300 специалистов можно добиться успеха там, где это удается не каждому.
            </Col>
          </Row>
        </Container>
      </div>
      <div className={styles.subheader}>
        НАШИ СПЕЦИАЛИСТЫ
      </div>
      <div className="d-flex justify-content-center"><Container className={styles.idea}>
        Мы приверженцы идеи что успех на финансовых рынках достигается не только при получении экономического образования. Успешный опыт работы, управления, как своим капиталом, так и доверенными средствами, является основой для начала работы в нашей команде. С 2017-го года, торговля на рынке стала для нас площадкой для демонстрации своего профессионализма и богатых навыков.
      </Container></div>
      <About/>
    </>
  );
}

export default AboutGlobal;
