import React from 'react';
import {Switch, Route} from 'react-router-dom';
import styles from './styles.module.scss';
import Main from './tabs/Main';
import Footer from './Footer';
import Menu from './Menu';
import AboutGlobal from './tabs/AboutGlobal';
import Career from './tabs/Career';
import AwardsGlobal from './tabs/AwardsGlobal';
import WhyUsGlobal from './tabs/WhyUsGlobal';
import AdvantagesGlobal from './tabs/AdvantagesGlobal';
import LoginTab from './tabs/LoginTab';
import RegisterTab from './tabs/RegisterTab';
import Contacts from './tabs/Contacts';
import Consultant from "./tabs/Consultant";
import Portfel from "./Portfel";
import Fund from "./tabs/Fund";

const Landing = ({defaultTabIndex}) => {
  return (
    <>
      <Menu/>
      <div className={styles.container}>
        <Switch>
          <Route path={`/`} exact component={Main}/>
          <Route path={`/about`} component={AboutGlobal}/>

          <Route path={`/awards`} component={AwardsGlobal}/>
          <Route path={`/whyUs`} component={WhyUsGlobal}/>
          <Route path={`/advantages`} component={AdvantagesGlobal}/>

          <Route path={`/assistant`} component={Consultant}/>
          <Route path={`/portfel`} component={Portfel}/>
          <Route path={`/fund`} component={Fund}/>

          <Route path={`/career`} component={Career}/>
          <Route path={`/contacts`} component={Contacts}/>

          <Route path={`/login`} component={LoginTab}/>
          <Route path={`/register`} component={RegisterTab}/>
        </Switch>
        <Footer/>
      </div>
    </>
  );
};

export default Landing;
