import React, {forwardRef, useEffect, useRef, useState} from "react";
import getRefRect from "../../utils/getRefRect";
import cn from "classnames";
import styles from "../../components/Landing/Tabs/style.module.css";
import TouchableOpacity from "../core/TouchableOpacity";
import {useHistory} from 'react-router-dom';

export const DropDown = forwardRef(({info=[], showing, setShowing, tabRef, containerRef, setMouseOverDropDown}, ref) => {
  const container = containerRef && containerRef.current && getRefRect(containerRef);
  const tab = tabRef && tabRef.current && getRefRect(tabRef);

  const history = useHistory();

  const [used, setUsed] = useState(false);

  return (
    <div
      ref={ref}
      className={cn(styles.dropdown, showing && styles.showing)}
      style={tab && container && {left: tab.left, top: tab.bottom}}
    >
      {showing && info.map(({name, action, href, path}) => {
        const element = (
          <TouchableOpacity
            key={name}
            onClick={() => {
              // action && action();
              href && window.open(href, '_blank');
              path && history.push(path);
              // setUsed(true);
              setShowing(false);
            }}
            className={styles.dropItem}
          >
            {name}
          </TouchableOpacity>
        );

        return element;

        return (
          href ?
            <a href={href}>
              {element}
            </a> : element
        );
      })}
    </div>
  );
});

function useDropDown({dropDown, ref, containerRef}) {
  const [isDropDown, setDropDown] = useState(false);

  const dropDownRef = useRef(null);

  const onMouseEnter = () => {
    setDropDown(true);
  };

  const onMouseOut = ({target, relatedTarget}) => {
    if (ref.current.contains(target) && !dropDownRef.current.contains(relatedTarget)) {
      setDropDown(false);
    }
  };

  const onDropDownMouseOut = ({target, relatedTarget}) => {
    if (dropDownRef.current.contains(target) && !dropDownRef.current.contains(relatedTarget) && !ref.current.contains(relatedTarget)) {
      setDropDown(false);
    }
  };

  useEffect(() => {
    if (dropDown) {
      ref.current.addEventListener('mouseenter', onMouseEnter);
      ref.current.addEventListener('mouseout', onMouseOut);
      dropDownRef.current.addEventListener('mouseout', onDropDownMouseOut);
    }
  }, [ref]);

  const dropDownElement = <DropDown
    ref={dropDownRef}
    info={dropDown}
    tabRef={ref}
    showing={isDropDown}
    setShowing={setDropDown}
    containerRef={containerRef}
    onMouseOut={onMouseOut}
  />;

  return dropDownElement;
}

export default useDropDown;
